/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBoardGridConfig } from '../../../configs/grid-configs';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { getViewByProperty, tweenToCell } from '../../../utils';
import { GiftSetView } from './gift-set-view';

export class BoardView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._giftsView = null;

    this._build();

    lego.event
      .on(ModelEvents.GameModel.ChosenMaidenReactionUpdate, this._onChosenMaidenReactionUpdate, this)
      .on(ViewEvents.SlideView.HideComplete, this._onSliderViewHideComplete, this);
  }

  get name() {
    return 'BoardView';
  }

  getGridConfig() {
    return getBoardGridConfig();
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildGiftsView();
  }

  _buildGiftsView() {
    const giftsView = new GiftSetView();
    this.setChild('gifts', (this._giftsView = giftsView));
  }

  _onChosenMaidenReactionUpdate(reaction) {
    this._maiden.reactToGift(reaction);
  }

  _onSliderViewHideComplete(uuid) {
    const sliderView = getViewByProperty('name', 'SliderView');
    const maiden = sliderView.getMaidenByUuid(uuid);
    const { position, parent } = maiden;

    const { x, y } = this.toLocal(position, parent);
    this.setChild('cell', (this._maiden = maiden));
    maiden.position.set(x, y);
    maiden.scale.set(parent.scale.x);
    tweenToCell(this, this._maiden, 'maiden', 500).onComplete.add(() => {
      lego.event.emit(ViewEvents.BoardView.MaidenMovementComplete);
    });
    this.bringToTop(this._giftsView);
  }
}
