/* eslint-disable class-methods-use-this */
import { getCTANoLoseGridConfig } from '../../configs/grid-configs';
import { CTARevealedCharacter } from './cta-revealed-character';
import { CTAView } from './cta-view';

export class CTARevealedView extends CTAView {
  constructor(isHandcuffed, maiden) {
    super();

    this._blocker = null;

    this.build(this.getGridConfig(), isHandcuffed, maiden);
  }

  getGridConfig() {
    return getCTANoLoseGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    if (this._playBtn) {
      this._playBtn.destroy();
      this.buildPlayBtn(this._btnText);
    }

    super.rebuild(config);
  }

  destroy() {
    this._blocker.destroy();
    super.destroy();
  }

  show(force = false) {
    this._spine.show();
    super.show(force);
  }

  build(config, isHandcuffed, maiden) {
    this._btnText = isHandcuffed ? "Show her who's King" : 'Enjoy the night!';
    const bg = isHandcuffed ? 'bg/cta_handcuff.jpg' : 'bg/cta-no-lose.jpg';
    super.build(config);
    this.buildBg(bg);
    this.buildBlocker(0.5);
    this._buildMaiden(maiden, isHandcuffed);
    this.buildPlayBtn(this._btnText);

    this.alpha = 0;
  }

  _buildMaiden(maiden, isHandcuffed) {
    const spine = new CTARevealedCharacter(maiden, isHandcuffed);
    this.setChild('characters', (this._spine = spine));
  }
}
