import { lego } from '@armathai/lego';
import { GameState } from '../../constants';
import { setChosenGiftCommand } from './set-chosen-gift-command';
import { setGameStateCommand } from './set-game-state-command';

export function onGiftChooseCommand(name) {
  lego.command
    .payload(GameState.GiftReaction)
    .execute(setGameStateCommand)

    .payload(name)
    .execute(setChosenGiftCommand);
}
