// @ts-nocheck
/* eslint-disable class-methods-use-this */
// eslint-disable-next-line max-classes-per-file
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAContainerGridConfig } from '../../configs/grid-configs';
import { AdStatus, CTAType } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { CTALoseView } from './cta-lose-view';
import { CTAMainView } from './cta-main-view';
import { CTARevealedView } from './cta-revealed-view';

export class CTAContainer extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this._chosenMaiden = null;
    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAContainerGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  destroyCta() {
    this._cta && this._cta.destroy();
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._build();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      case AdStatus.Retry:
        this.destroyCta();
        break;
      default:
    }
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildCta();
    this._cta.show();
  }

  _buildCta() {
    const { type } = store.ad.cta;
    this._chosenMaiden = store.game.chosenMaiden;

    switch (type) {
      case CTAType.Lose:
        this._cta = new CTALoseView(this._chosenMaiden);
        break;
      case CTAType.Main:
        this._cta = new CTAMainView(false, this._chosenMaiden);
        break;
      case CTAType.MainHandcuff:
        this._cta = new CTAMainView(true, this._chosenMaiden);
        break;
      case CTAType.Revealed:
        this._cta = new CTARevealedView(false, this._chosenMaiden);
        break;
      case CTAType.RevealedHandcuff:
        this._cta = new CTARevealedView(true, this._chosenMaiden);
        break;

      default:
        break;
    }

    this.setChild('container', this._cta);
  }
}
