/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import {
  getChooseButtonConfig,
  getLeftArrowButtonConfig,
  getRightArrowButtonConfig
} from '../../../configs/button-configs';
import { getSliderGridConfig } from '../../../configs/grid-configs';
import { InputPriority } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { hideToBottom, hideToLeft, hideToRight, makePixel, pulse } from '../../../utils';
import { Button } from '../../../utils/button/button';
import { CarouselComponent } from './carousel-component';
import { MaidenView } from './maiden-view';

export class SliderView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this._maidens = [];
    this._build();

    lego.event
      .on(ModelEvents.SliderModel.MaidensUpdate, this._onMaidensUpdate, this)
      .on(ModelEvents.GameModel.ChosenMaidenUpdate, this._onChosenMaidenUpdate, this);
  }

  get name() {
    return 'SliderView';
  }

  getHintPosition() {
    return new Phaser.Point(this._chooseButton.centerX + this._chooseButton.width / 3, this._chooseButton.centerY);
  }

  getGridConfig() {
    return getSliderGridConfig();
  }

  getNearestMaiden() {
    return this._carousel.nearestItem;
  }

  getMaidenByUuid(uuid) {
    return this._maidens.find(maiden => maiden.uuid === uuid);
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    !!this._carousel && this._carousel.rebuild();
    super.rebuild(config);
  }

  hide() {
    hideToRight(this._rightArrow, 0, 200);
    hideToLeft(this._leftArrow, 0, 200);

    hideToBottom(this._chooseButton, 0, 500).onComplete.add(() => {
      lego.event.emit(ViewEvents.SlideView.HideComplete, this.getNearestMaiden().uuid);
      this.destroy();
    });
  }

  _removeInputs() {
    this._carousel.removeInputs();
    this._leftArrow.switchInput(false);
    this._rightArrow.switchInput(false);
    this._chooseButton.switchInput(false);
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildHitArea();
    this._buildArrows();
    this._buildChooseButton();
  }

  _buildHitArea() {
    const hitArea = makePixel({ alpha: 0 });
    hitArea.inputEnabled = true;
    hitArea.name = 'Slider';
    hitArea.input.priorityID = InputPriority.Game;
    this.setChild('hit_area', (this._hitArea = hitArea));
  }

  _buildArrows() {
    this._rightArrow = new Button(getRightArrowButtonConfig());
    this._leftArrow = new Button(getLeftArrowButtonConfig());

    pulse(this._leftArrow);
    pulse(this._rightArrow);

    this.setChild('leftArrow', this._leftArrow);
    this.setChild('rightArrow', this._rightArrow);
  }

  _buildChooseButton() {
    this._chooseButton = new Button(getChooseButtonConfig());

    this._chooseButton.onClick.add(this._onMaidenChoose, this);
    this._chooseButton.name = 'choose_button';
    this.setChild('button', this._chooseButton);
  }

  _onChosenMaidenUpdate() {
    const { gameObjects, nearestItemIndex } = this._carousel;

    const right = gameObjects[(nearestItemIndex + 1) % gameObjects.length];
    const leftIndex = nearestItemIndex - 1 < 0 ? gameObjects.length - 1 : nearestItemIndex - 1;
    const left = gameObjects[leftIndex];
    hideToLeft(left);
    hideToRight(right);
  }

  _onMaidensUpdate(maidens) {
    this._buildMaidens(maidens);

    this.setChild(
      'carousel',
      (this._carousel = new CarouselComponent(this._maidens, {
        left: this._leftArrow,
        right: this._rightArrow,
        choose: this._chooseButton,
        hitArea: this._hitArea
      }))
    );

    this.bringToTop(this._leftArrow);
    this.bringToTop(this._rightArrow);
    this.bringToTop(this._chooseButton);
    this._carousel.nearestItemUpdate.add(this._onCarouselNearestItemUpdate, this);
    // this._carousel.focusOnNearestComplete.add(this._onFocusOnNearestComplete, this);
    // postRunnable(() => this._onCarouselNearestItemUpdate(this._carousel.nearestItem.uuid));
  }

  _onCarouselNearestItemUpdate() {
    //
  }

  _buildMaidens(maidens) {
    maidens.forEach(m => {
      const maiden = new MaidenView(m.viewConfig, m.name, m.uuid);
      this._maidens.push(maiden);
    });
  }

  _onMaidenChoose() {
    lego.event.emit(ViewEvents.SlideView.ChooseClick);
    this._removeInputs();
    lego.event.emit(ViewEvents.MaidenView.Choose, this.getNearestMaiden().uuid);
  }
}
