import { CellAlign } from '@armathai/phaser2-grid';

export function getForegroundGridLandscapeConfig() {
  return {
    // debug: { color: 0xd95027 },
    name: 'foreground',
    cells: [
      {
        name: 'logo',
        bounds: { x: 0, y: 0, height: 0.135, width: 0.26 },
        offset: { x: 20, y: 20 }
      },
      {
        name: 'lose_logo',
        bounds: { x: 0.05, y: 0.077, height: 0.22, width: 0.4 }
      },
      {
        name: 'no_lose_logo',
        bounds: { x: 0.06, y: 0.275, height: 0.22, width: 0.4 }
      },
      {
        name: 'sound',
        bounds: { x: 1 - 0.035 - 0.07, y: 0.8, width: 0.07, height: 0.1 }
      },
      {
        name: 'tutorial',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

export function getForegroundGridPortraitConfig() {
  const logoH = CI_API.Globals.PARAMS.cta_btn_persistent ? 0.14 : 0.2;
  return {
    // debug: { color: 0xd95027 },
    name: 'foreground',
    cells: [
      {
        name: 'logo',
        bounds: { x: 0, y: 0, width: 1, height: logoH },
        offset: { y: 15 }
      },
      {
        name: 'lose_logo',
        bounds: { x: 0, y: 0, width: 1, height: 0.14 },
        offset: { y: 15 }
      },
      {
        name: 'no_lose_logo',
        bounds: { x: 0, y: 0, width: 1, height: 0.14 },
        offset: { y: 15 }
      },
      {
        name: 'sound',
        bounds: { x: 0, y: 0.1, width: 0.96, height: 0.06 },
        align: CellAlign.RightCenter
      },
      {
        name: 'tutorial',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}
