import { lego } from '@armathai/lego';
import { AdStatus, GameOverReasons, MaidenReaction } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

let sound;

function play(id, volume = 1) {
  sound.play(id, volume);
}

function stop(id) {
  sound.stop(id);
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }
  sound = soundLoader.addAudioSprite('audio');
}

function playGameTheme() {
  play('girl-room', 0.7);
}

function onCarouselMove() {
  play('bookflip');
}

function onMaidenChoose() {
  play('choose');
}

function playClick() {
  play('click');
}

function playPop() {
  play('pop');
}

function playReactionSound(reaction) {
  switch (reaction) {
    case MaidenReaction.RightGift:
      play('reward');
      break;
    case MaidenReaction.WrongGift:
      play('choose');
      break;
    case MaidenReaction.Handcuff:
      play('receive');
      break;

    default:
      break;
  }
}

function onAdStatusUpdate(status) {
  if (status === AdStatus.Cta)
    switch (store.ad.cta.reason) {
      case GameOverReasons.Idled:
      case GameOverReasons.WrongGift:
        stop('girl-room');
        play('courtyard', 0.7);
        break;
      default:
    }
}

function playCtaBtnSound(state) {
  play(state, 1);
}

export function SoundObservant() {
  lego.event.once(ViewEvents.Game.UserInteraction, playGameTheme, this);
  lego.event
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this)
    .on(ViewEvents.CarouselView.Move, onCarouselMove, this)
    .on(ViewEvents.MaidenView.Choose, onMaidenChoose, this)
    .on(ModelEvents.GameModel.ChosenMaidenReactionUpdate, playReactionSound, this)
    .on(ViewEvents.SlideView.ChooseClick, playClick, this)
    .on(ViewEvents.SlideView.ArrowClick, playClick, this)
    .on(ViewEvents.BubbleComponent.ShowComplete, playPop, this)
    .on(ModelEvents.AdModel.StatusUpdate, onAdStatusUpdate, this)
    .on(ViewEvents.CtaView.PlayBtnSound, playCtaBtnSound, this);
}
