import { MaidenReaction } from '../../constants';
import { ObservableModel } from '../observable-model';

export class MaidenModel extends ObservableModel {
  constructor(config) {
    super('MaidenModel');
    const { name, prefers, viewConfig } = config;
    this._name = name;
    this._viewConfig = viewConfig;
    this._prefers = prefers;
    this._reaction = MaidenReaction.Unknown;

    this.makeObservable();
  }

  get name() {
    return this._name;
  }

  get prefers() {
    return this._prefers;
  }

  get reaction() {
    return this._reaction;
  }

  get viewConfig() {
    return this._viewConfig;
  }

  set reaction(value) {
    this._reaction = value;
  }
}
