import { lego } from '@armathai/lego';
import { store } from '../../models/store';
import { showCtaCommand } from '../ad/cta/show-cta-command';

export function onMaidenReactionCompleteCommand() {
  const { chosenMaidenReaction } = store.game;
  lego.command.payload(chosenMaidenReaction, 1500).execute(showCtaCommand);
}
