import { CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getBackgroundGridLandscapeConfig() {
  const padY = isSquareLikeScreen() ? -0.2 : -0.5;
  return {
    // debug: { color: 0xd95027 },
    name: 'background',
    cells: [
      {
        name: 'game',
        scale: CellScale.Envelop,
        padding: { y: padY },
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

export function getBackgroundGridPortraitConfig() {
  const paddingY = isSquareLikeScreen() ? -0.2 : 0;
  return {
    // debug: { color: 0xd95027 },
    name: 'background',
    cells: [
      {
        name: 'game',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        padding: { x: -0.5, y: paddingY },
        scale: CellScale.Envelop
      }
    ]
  };
}
