import { lego } from '@armathai/lego';
import { CTAType, MaidenReaction } from '../constants';
import { store } from '../models/store';
import { setCTATypeCommand } from './ad/cta/set-cta-type-command';

export function setCTATypeFromChosenGiftCommand() {
  const { chosenMaidenReaction: reaction } = store.game;
  const { revealing_cta: revealingCta } = CI_API.Globals.PARAMS;

  let ctaType = CTAType.Lose;
  switch (reaction) {
    case MaidenReaction.Handcuff:
      ctaType = revealingCta ? CTAType.RevealedHandcuff : CTAType.MainHandcuff;
      break;
    case MaidenReaction.RightGift:
      ctaType = revealingCta ? CTAType.Revealed : CTAType.Main;
      break;

    default:
      break;
  }

  lego.command.payload(ctaType).execute(setCTATypeCommand);
}
