import { lego } from '@armathai/lego';
import { AdStatus, AdViewState, GameOverReasons } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

const toSnakeCase = str =>
  str &&
  str
    .match(/[A-Z]{2,}(?=[A-Z][a-z]+[0-9]*|\b)|[A-Z]?[a-z]+[0-9]*|[A-Z]|[0-9]+/g)
    .map(x => x.toLowerCase())
    .join('_');

function logCtaOverride() {
  analytics.logProgress('cta_override', 'restart');
}

function logIdledCta() {
  analytics.logProgress('objective', 'idle');
}

function logRightGift() {
  analytics.logProgress('objective', 'completed');
}

function logWrongGift() {
  analytics.logProgress('objective', 'failed');
}

function logHandcuff() {
  analytics.logProgress('objective', 'handcuff');
}

function logGameOverType(reason) {
  // eslint-disable-next-line no-console
  analytics.logProgress('reason', reason);

  // analytics.logProgress('items_to_cta', '');
}

function logCta() {
  switch (store.ad.cta.reason) {
    case GameOverReasons.Idled:
      logIdledCta();
      break;
    case GameOverReasons.RightGift:
      logRightGift();
      break;
    case GameOverReasons.WrongGift:
      logWrongGift();
      break;
    case GameOverReasons.Handcuff:
      logHandcuff();
      break;
    default:
      logGameOverType(store.ad.cta.reason);
    // throw new Error(`Unknown CTA reason`);
  }
}

function onGameDown(pointer) {
  if (!pointer.targetObject || !pointer.targetObject.sprite) {
    analytics.logClick('first_interaction_click', 'unknown');
    return;
  }
  analytics.logClick('first_interaction_click', toSnakeCase(pointer.targetObject.sprite.name));
}

function onCtaScreenClick() {
  analytics.logClick('cta_click', 'cta_overlay');
}

function onCtaPlayClick() {
  analytics.logClick('cta_click', 'cta_button');
}

function onPersistentCtaClick() {
  analytics.logClick('cta_click', 'persistent_cta_button');
}

function onTutorialCompleteUpdate(complete) {
  if (complete) {
    analytics.logProgress('tutorial', 'complete');
  }
}

function onTutorialSkipUpdate(skip) {
  if (skip) {
    analytics.logProgress('tutorial', 'skipped');
  }
}

function onViewStateUpdate(state) {
  switch (state) {
    case AdViewState.Create:
      CI_API.game.input.onDown.addOnce(onGameDown, this);
      lego.event.off(ModelEvents.AdModel.ViewStateUpdate, onViewStateUpdate, this);
      break;
    default:
  }
}

function onAdStatusUpdate(status) {
  switch (status) {
    case AdStatus.Restart:
      logCtaOverride();
      break;
    default:
  }
}

function onCtaVisibleUpdate(visible) {
  if (!visible) {
    return;
  }

  logCta();
}

function onChosenMaidenUpdate(maiden) {
  analytics.logAction('which_maiden_chosen', maiden.name);
}

function onChosenGiftUpdate(gift) {
  if (CI_API.Globals.PARAMS.handcuffs_cta) {
    analytics.logAction('handcuffs_chosen', gift.name === 'handcuffs' ? 'true' : 'false');
  }
}

export function AnalyticsObservant() {
  lego.event
    .on(ViewEvents.CtaView.ScreenClick, onCtaScreenClick, this)
    .on(ViewEvents.CtaView.PlayClick, onCtaPlayClick, this)
    .on(ViewEvents.PersistentCtaView.Click, onPersistentCtaClick, this)
    .on(ModelEvents.TutorialModel.CompleteUpdate, onTutorialCompleteUpdate, this)
    .on(ModelEvents.TutorialModel.SkipUpdate, onTutorialSkipUpdate, this)
    .on(ModelEvents.AdModel.ViewStateUpdate, onViewStateUpdate, this)
    .on(ModelEvents.AdModel.StatusUpdate, onAdStatusUpdate, this)
    .on(ModelEvents.CtaModel.VisibleUpdate, onCtaVisibleUpdate, this)
    .on(ModelEvents.BoardModel.ChosenGiftUpdate, onChosenGiftUpdate, this)
    .on(ModelEvents.GameModel.ChosenMaidenUpdate, onChosenMaidenUpdate, this);
}
