/* eslint-disable class-methods-use-this */
import { getCTALoseGridConfig } from '../../configs/grid-configs';
import { getCTALoseBubbleImageConfig } from '../../configs/image-configs';
import { getWineGirlSpineConfig, Spines } from '../../configs/spine-configs';
import { getCtaLoseTextConfig } from '../../configs/text-configs';
import { CTALoseCharacters } from './cta-lose-characters';
import { CTAView } from './cta-view';

export class CTALoseView extends CTAView {
  constructor(maiden) {
    super();

    this._blocker = null;

    this._build(this.getGridConfig(), maiden);
  }

  getGridConfig() {
    return getCTALoseGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    if (this._playBtn) {
      this._playBtn.destroy();
      this.buildPlayBtn(this._btnText);
    }

    super.rebuild(config);
  }

  destroy() {
    this._blocker.destroy();
    super.destroy();
  }

  _build(config, maiden, blockerAlpha = 0.5) {
    this._btnText = 'Find another maiden';
    this.build(config);
    this.buildBg('bg/cta-lose.jpg');
    this.buildBlocker(blockerAlpha);

    this._buildCharacters(maiden);
    this.buildPopup({ imageConfig: getCTALoseBubbleImageConfig, textConfig: getCtaLoseTextConfig, isLost: true });
    this.buildPlayBtn(this._btnText);
    this.alpha = 0;
  }

  _buildCharacters(maiden) {
    const config = {
      spineConf: maiden ? maiden.viewConfig.spineData.config : getWineGirlSpineConfig(),
      anim: maiden ? maiden.viewConfig.spineData.animations.happyTrack0 : Spines.FlowerGirl.Animations.happyTrack0
    };

    this._characters = new CTALoseCharacters(config);

    this.setChild('characters', this._characters);
  }
}
