/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCTABubbleImageConfig } from '../../../configs/image-configs';
import {
  getHandcuffReactionTextConfig,
  getRightReactionTextConfig,
  getWrongReactionTextConfig
} from '../../../configs/text-configs';
import { MaidenReaction } from '../../../constants';
import { ViewEvents } from '../../../events/view-events';
import { delayRunnable, hideToBottom, makePixel, makeSpine, removeRunnable, tintSpine } from '../../../utils';
import { Container } from '../../../utils/container';
import { Bubble } from '../../bubble';

const dimOffset = {
  // eslint-disable-next-line camelcase
  Jewelry_girl: { x: -95, y: 6 },
  // eslint-disable-next-line camelcase
  Flower_girl: { x: -164, y: -23 },
  // eslint-disable-next-line camelcase
  Wine_girl: { x: 0, y: -13 }
};

export class MaidenView extends Container {
  constructor(config, name, uuid) {
    super();

    const { spineData, scale } = config;
    this._spineData = spineData;
    this._scale = scale;
    this._name = name;
    this.uuid = uuid;

    this._build();
  }

  get name() {
    return this._name;
  }

  getBounds() {
    return this._hitArea.getBounds();
  }

  hide() {
    hideToBottom(this).onComplete.add(() => this.destroy());
  }

  destroy() {
    removeRunnable(this._reactionRunnable);
    super.destroy();
  }

  reactToGift(reaction) {
    const textConfig = this._getTextConfig(reaction);
    const animation = this._getAnimation(reaction);

    this._spine.setAnimationByName(0, animation, true);
    if (reaction === MaidenReaction.RightGift) {
      this._spine.setAnimationByName(1, this._spineData.animations.heartsTrack1, true);
    }

    this._buildBubble(textConfig);

    this._reactionRunnable = delayRunnable(500, () => {
      lego.event.emit(ViewEvents.MaidenView.ReactionComplete);
    });
  }

  updateSaturation(fraction) {
    this._saturation.alpha = fraction;
  }

  updateDim(fraction) {
    this._dim.alpha = fraction;
  }

  pause() {
    const { animations } = this._spineData;
    this._spine.setToSetupPose();
    this._spine.setAnimationByName(0, animations.idleTrack0, true);
    // @ts-ignore
    this._spine.state.timeScale = 0;
  }

  resume() {
    // @ts-ignore
    this._spine.state.timeScale = 1;
  }

  _build() {
    this._buildSpine();
    this._buildHitArea();
    this._buildDim();
  }

  _buildHitArea() {
    const hitArea = makePixel({ x: 0, y: 0, width: 420, height: 730, alpha: 0, tint: 0x0 });
    hitArea.anchor.set(0.5, 1);

    this.addChild((this._hitArea = hitArea));
  }

  _buildSpine() {
    const { animations, config } = this._spineData;
    const spine = makeSpine(config);
    const { AngryTrack0, blushTrack0, happyTrack0, idleTrack0 } = animations;

    spine.setToSetupPose();
    spine.setAnimationByName(0, animations.idleTrack0, true);
    spine.scale.copyFrom(this._scale);

    this.addChild((this._spine = spine));
    this._spine.setMixByName(idleTrack0, happyTrack0, 0.4);
    this._spine.setMixByName(idleTrack0, blushTrack0, 0.4);
    this._spine.setMixByName(idleTrack0, AngryTrack0, 0.4);
  }

  _buildDim() {
    const { animations, config } = this._spineData;
    const spine = makeSpine(config);
    this.addChild(spine);
    spine.setToSetupPose();
    spine.setAnimationByName(0, animations.idleTrack0, true);
    // @ts-ignore
    spine.state.timeScale = 0;
    spine.scale.copyFrom(this._scale);

    tintSpine(spine, 0x000000);
    const rt = this.game.add.renderTexture(1000, 1000);
    rt.renderXY(spine, 304, 730, true);
    const { left, top } = this._spine;
    const { x, y } = dimOffset[this._name];

    // DIM
    const dimSprite = this.game.add.sprite(0, 100, rt);
    this.addChild((this._dim = dimSprite));
    dimSprite.position.set(left + x, top + y);

    // SATURATION
    const saturation = this.game.add.sprite(0, 100, rt);
    this.addChild((this._saturation = saturation));
    saturation.position.set(left + x, top + y);
    saturation.blendMode = Phaser.blendModes.COLOR;

    spine.destroy();
  }

  _buildBubble(textConfig) {
    const bubble = new Bubble({
      imageConfig: getCTABubbleImageConfig,
      textConfig
    });
    bubble.position.set(-bubble.width / 2 - 30, -this._spine.height + bubble.height - 120);

    this.addChild((this._bubble = bubble));
    this.bringToTop(this._spine);

    const tw = this.game.add
      .tween(this._bubble.scale)
      .from({ x: 0, y: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);

    tw.onStart.add(() => {
      lego.event.emit(ViewEvents.BubbleComponent.ShowComplete);
    });
  }

  _getTextConfig(reaction) {
    let textConfig = getWrongReactionTextConfig;

    switch (reaction) {
      case MaidenReaction.RightGift:
        textConfig = getRightReactionTextConfig;
        break;
      case MaidenReaction.Handcuff:
        textConfig = getHandcuffReactionTextConfig;
        break;
      default:
        break;
    }

    return textConfig;
  }

  _getAnimation(reaction) {
    let animation = this._spineData.animations.AngryTrack0;

    switch (reaction) {
      case MaidenReaction.RightGift:
        animation = this._spineData.animations.happyTrack0;
        break;
      case MaidenReaction.Handcuff:
        animation = this._spineData.animations.blushTrack0;
        break;
      default:
        break;
    }
    return animation;
  }
}
