import { ObservableModel } from '../../observable-model';

export class GiftModel extends ObservableModel {
  constructor(config) {
    super('GiftModel');

    this._name = config.name;
    this._frame = config.frame;
  }

  get frame() {
    return this._frame;
  }

  get name() {
    return this._name;
  }
}
