import { lego } from '@armathai/lego';
import { findMaidensReactionCommand } from '../check-gift-compatibility-command';
import { setMaidenReactionCommand } from '../game/set-maiden-reaction-command';

export function onChosenGiftUpdateCommand() {
  lego.command.payload(findMaidensReactionCommand()).execute(setMaidenReactionCommand);
}
