import { lego } from '@armathai/lego';
import {
  getColorPersistentCtaButtonConfig,
  getDefaultPersistentCtaButtonConfig,
  getPushPersistentCtaButtonConfig
} from '../../configs/button-configs';
import { getPersistentSparkleEmitterConfig } from '../../configs/particles-configs';
import { ViewEvents } from '../../events/view-events';
import { makeEmitter, postRunnable, pulse } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class PersistentCTAView extends Container {
  constructor() {
    super();
    this._build();
  }

  getBounds() {
    return this._btn.getBounds();
  }

  destroy() {
    this._removeTweens();
    if (this._sparklesEmitter) {
      this._sparklesEmitter.paused = true;
    }

    super.destroy();
  }

  _build() {
    this._buildButton();
    this._buildEffects();
  }

  _buildButton() {
    const {
      reactive_cta_button: reactiveCtaButton,
      reactive_button_state: reactiveButtonState,
      click_sound: ctaBtnClickSound
    } = CI_API.Globals.PARAMS;

    if (reactiveCtaButton) {
      switch (reactiveButtonState) {
        case 'push':
          this._buildPushPlayBtn();
          break;
        case 'color':
          this._buildColorPlayBtn();
          break;
        case 'animate':
          this._buildAnimatePlayBtn();
          break;
        case 'none':
          this._buildDefaultPlayBtn();
          break;
        default:
          break;
      }

      switch (ctaBtnClickSound) {
        case '90s':
        case 'Apple':
        case 'Laptop':
          this._btn.onClick.add(() => {
            lego.event.emit(ViewEvents.CtaView.PlayBtnSound, ctaBtnClickSound);
          });
          break;
        case 'none':
          break;
        default:
          break;
      }
    } else {
      this._buildDefaultPlayBtn();
    }

    this._btn.onClick.add(() => lego.event.emit(ViewEvents.PersistentCtaView.Click), this);
    this.addChild(this._btn);
    this.bringToTop(this._btn);
  }

  _buildDefaultPlayBtn() {
    this._btn = new Button(getDefaultPersistentCtaButtonConfig());
  }

  _buildPulsePlayBtn() {
    this._btn = new Button(getDefaultPersistentCtaButtonConfig());
    postRunnable(() => pulse(this._btn));
  }

  _buildPushPlayBtn() {
    this._btn = new Button(getPushPersistentCtaButtonConfig());
  }

  _buildColorPlayBtn() {
    this._btn = new Button(getColorPersistentCtaButtonConfig());
  }

  _buildAnimatePlayBtn() {
    this._btn = new Button(getDefaultPersistentCtaButtonConfig());
    this._btn.onClick.add(this._animatePlayBtn, this);
  }

  _animatePlayBtn() {
    this._btn.scale.set(1, 1);

    this.game.add.tween(this._btn.scale).to({ y: 1.15 }, 100, Phaser.Easing.Back.Out, true, 0, 0, true);
    this.game.add.tween(this._btn.scale).to({ x: 1.15 }, 150, Phaser.Easing.Back.Out, true, 100, 0, true);
  }

  _buildEffects() {
    const { pcta_effects: effect } = CI_API.Globals.PARAMS;

    switch (effect) {
      case 'sparkle':
        this._buildSparklesEffect();
        break;
      case 'pulse':
        this._addPulse();
        break;
      case 'both':
        this._buildSparklesEffect();
        this._addPulse();
        break;

      default:
        break;
    }
  }

  _buildSparklesEffect() {
    this._sparklesEmitter = makeEmitter(getPersistentSparkleEmitterConfig(this, new Phaser.Point(0, 0)));
    this.bringToTop(this._btn);
  }

  _addPulse() {
    pulse(this._btn);
  }

  _removeTweens() {
    this.game.tweens.removeFrom(this._btn);
    this.game.tweens.removeFrom(this._btn.scale);
  }
}
