import { GameState } from '../../constants';
import { ObservableModel } from '../observable-model';
import { BoardModel } from './board/board-model';
import { SliderModel } from './slider/slider-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._maidens = [];
    this._chosenMaiden = null;
    this._chosenMaidenReaction = null;
    this._boardModel = null;
    this._sliderModel = null;
    this._state = GameState.Unknown;
    this.makeObservable();
  }

  get boardModel() {
    return this._boardModel;
  }

  get sliderModel() {
    return this._sliderModel;
  }

  get maidens() {
    return this._maidens;
  }

  set maidens(value) {
    this._maidens = value;
  }

  get chosenMaiden() {
    return this._chosenMaiden;
  }

  set chosenMaiden(value) {
    this._chosenMaiden = value;
  }

  get chosenMaidenReaction() {
    return this._chosenMaidenReaction;
  }

  set chosenMaidenReaction(value) {
    this._chosenMaidenReaction = value;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initialize() {
    this._state = GameState.Choose;
    this._boardModel = new BoardModel();
    this._boardModel.initialize();

    this._sliderModel = new SliderModel();
    this._sliderModel.initialize();

    // this._initMaidens();
  }

  destroy() {
    if (this._boardModel) {
      this._boardModel.destroy();
      this._boardModel = null;
    }

    if (this._sliderModel) {
      this._sliderModel.destroy();
      this._sliderModel = null;
    }
  }

  // getMaidenByName(name) {
  //   return this._maidens.find(m => m.name === name);
  // }

  // _initMaidens() {
  //   this._maidens = maidensConfig.map(config => new MaidenModel(config));
  // }
}
