import { maidensConfig } from '../../../configs';
import { ObservableModel } from '../../observable-model';
import { MaidenModel } from '../maiden-model';

export class SliderModel extends ObservableModel {
  constructor() {
    super('SliderModel');

    this._maidens = null;
    this._nearestItem = null;

    this.initialize();

    this.makeObservable();
  }

  get maidens() {
    return this._maidens;
  }

  get nearestItem() {
    return this._nearestItem;
  }

  set nearestItem(item) {
    this._nearestItem = item;
  }

  getItemByUuid(uuid) {
    return this._maidens.find(item => item.uuid === uuid);
  }

  getMaidenByName(name) {
    return this._maidens.find(m => m.name === name);
  }

  updateNearestItem(uuid) {
    this._nearestItem = this.getItemByUuid(uuid);
  }

  initialize() {
    this._maidens = maidensConfig.map(config => new MaidenModel(config));
  }
}
