import { MaidenReaction } from '../constants';
import { store } from '../models/store';

export function findMaidensReactionCommand() {
  const { name: chosenGift } = store.game.boardModel.chosenGift;
  const { prefers } = store.game.chosenMaiden;

  let reaction;
  if (chosenGift === prefers) {
    reaction = MaidenReaction.RightGift;
  } else if (chosenGift !== prefers) {
    reaction = MaidenReaction.WrongGift;
  }

  if (chosenGift === 'handcuffs') {
    reaction = MaidenReaction.Handcuff;
  }

  return reaction;
}
