/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import {
  getCtaColorPlayButtonConfig,
  getCtaDefaultPlayButtonConfig,
  getCtaPushPlayButtonConfig
} from '../../configs/button-configs';
import { getBackgroundViewImageConfig } from '../../configs/image-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import { makeImage, makePixel, postRunnable, pulse } from '../../utils';
import { Button } from '../../utils/button/button';
import { Bubble } from '../bubble';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
  }

  getGridConfig() {
    return new Error();
  }

  show(force = false) {
    if (force) {
      this.alpha = 1;
    } else {
      this._show();
    }
  }

  buildPlayButton(config) {
    return this._buildButton(config, this._onPlayClick);
  }

  buildRetryButton(config) {
    return this._buildButton(config, this._onRetryClick);
  }

  buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = this.name;
    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }
  }

  buildPlayBtn(text) {
    const {
      reactive_cta_button: reactiveCtaButton,
      reactive_button_state: reactiveButtonState,
      click_sound: ctaBtnClickSound
    } = CI_API.Globals.PARAMS;

    if (reactiveCtaButton) {
      switch (reactiveButtonState) {
        case 'push':
          this._buildPushPlayBtn(text);
          break;
        case 'color':
          this._buildColorPlayBtn(text);
          break;
        case 'animate':
          this._buildAnimatePlayBtn(text);
          break;
        case 'none':
          this._buildDefaultPlayBtn(text);
          break;
        default:
          break;
      }

      switch (ctaBtnClickSound) {
        case '90s':
        case 'Apple':
        case 'Laptop':
          this._playBtn.onClick.add(() => {
            lego.event.emit(ViewEvents.CtaView.PlayBtnSound, ctaBtnClickSound);
          });
          break;
        case 'none':
          break;
        default:
          break;
      }
    } else {
      this._buildPulsePlayBtn(text);
    }
  }

  _buildDefaultPlayBtn(text) {
    this._playBtn = this.buildPlayButton(getCtaDefaultPlayButtonConfig(text));
    this.setChild('play_button', this._playBtn);
  }

  _buildPulsePlayBtn(text) {
    this._playBtn = this.buildPlayButton(getCtaDefaultPlayButtonConfig(text));
    this.setChild('play_button', this._playBtn);
    postRunnable(() => pulse(this._playBtn));
  }

  _buildPushPlayBtn(text) {
    this._playBtn = this.buildPlayButton(getCtaPushPlayButtonConfig(text));
    this.setChild('play_button', this._playBtn);
  }

  _buildColorPlayBtn(text) {
    this._playBtn = this.buildPlayButton(getCtaColorPlayButtonConfig(text));
    this.setChild('play_button', this._playBtn);
  }

  _buildAnimatePlayBtn(text) {
    this._playBtn = this.buildPlayButton(getCtaDefaultPlayButtonConfig(text));
    this._playBtn.onClick.add(this._animatePlayBtn, this);
    this.setChild('play_button', this._playBtn);
  }

  _animatePlayBtn() {
    this._playBtn.scale.set(1, 1);

    this.game.add.tween(this._playBtn.scale).to({ y: 1.2 }, 100, Phaser.Easing.Back.Out, true, 0, 0, true);
    this.game.add.tween(this._playBtn.scale).to({ x: 1.2 }, 150, Phaser.Easing.Back.Out, true, 100, 0, true);
  }

  buildPopup(config) {
    this._popup = new Bubble(config);
    this._popup.alpha = 0;
    this.setChild('popup', this._popup);
  }

  buildBg(key) {
    this._bg && this._bg.destroy();
    this._bg = makeImage(getBackgroundViewImageConfig(key));
    this.setChild('bg', this._bg);
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }

  _show() {
    const tw = this.game.add.tween(this).to({ alpha: 1 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
    tw.onComplete.add(() => {
      if (this._popup) {
        this.game.add.tween(this._popup).to({ alpha: 1 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
        this.game.add
          .tween(this._popup.scale)
          .from({ x: 0, y: 0 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false)
          .onStart.add(() => {
            lego.event.emit(ViewEvents.BubbleComponent.ShowComplete);
          });
      }
    });
  }
}
