import { isSquareLikeScreen } from '../../utils';

export function getTutorialSequenceGridLandscapeConfig() {
  const h0 = isSquareLikeScreen() ? 0.4 : 0.3;
  return {
    // debug: { color: 0xd95777 },
    name: 'tutorial_sequence',
    cells: [
      {
        name: '0',
        bounds: { x: 0.6, y: 0, width: 0.35, height: h0 },
        offset: { y: 15 }
      },
      {
        name: '1',
        bounds: { x: 0.47, y: 0.61, width: 0.43, height: 0.26 }
      }
    ]
  };
}

export function getTutorialSequenceGridPortraitConfig() {
  return {
    // debug: { color: 0xd95777 },
    name: 'tutorial_sequence',
    cells: [
      {
        name: '0',
        bounds: { x: 0, y: 0.23, width: 1, height: 0.17 }
      },
      {
        name: '1',
        bounds: { x: 0, y: 0.23, width: 1, height: 0.17 }
      }
    ]
  };
}
