/* eslint-disable class-methods-use-this */
import { getGuySpineConfig, Spines } from '../../configs/spine-configs';
import { makeSpine } from '../../utils';
import { Container } from '../../utils/container';

export class CTALoseCharacters extends Container {
  constructor(config) {
    super();

    this._build(config);
  }

  getBounds() {
    return new Phaser.Rectangle(-250, -755, 650, 750);
  }

  _build(config) {
    this._buildGuy();
    this._buildGirl(config);

    // const gr = this.game.add.graphics();
    // gr.beginFill(0xff0000, 0.5);
    // gr.drawRect(this.getBounds().x, this.getBounds().y, this.getBounds().width, this.getBounds().height);
    // gr.endFill();
    // this.addChild(gr);
  }

  _buildGuy() {
    const guy = makeSpine(getGuySpineConfig());
    guy.setAnimationByName(0, Spines.Guy.Animations.Idle, true);

    this.addChild((this._guy = guy));
  }

  _buildGirl(conf) {
    const { spineConf, anim } = conf;

    const girl = makeSpine(spineConf);
    girl.setAnimationByName(0, anim, true);
    girl.position.set(girl.width / 2, 80);
    this.addChild((this._girl = girl));
  }
}
