/* eslint-disable class-methods-use-this */
import { getGiftCircleImageConfig } from '../../../configs/image-configs';
import { InputPriority } from '../../../constants';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class GiftView extends Container {
  constructor(config) {
    super();

    this._config = config;
    this.onChoose = new Phaser.Signal();

    this._build();
  }

  get name() {
    return this._config.name;
  }

  show() {
    this.game.add.tween(this).to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
    this.game.add.tween(this.scale).from({ x: 0, y: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
  }

  _build() {
    this._buildGift();
    this._setInput();
    this.alpha = 0;
  }

  _buildGift() {
    this._buildBg();
    this._buildImage();
  }

  _buildBg() {
    const bg = makeImage(getGiftCircleImageConfig());
    this.addChild((this._bg = bg));
  }

  _buildImage() {
    const img = makeImage(this._config.frame);
    img.scale.set(1.3);
    this.addChild((this._img = img));
  }

  _setInput() {
    this._bg.inputEnabled = true;
    this._bg.input.priorityID = InputPriority.Game;
    this._bg.name = this.name;

    this._bg.events.onInputDown.add(this._onGiftClick, this);
  }

  _onGiftClick() {
    this.onChoose.dispatch(this.name);
  }

  _removeTw() {
    this.game.tweens.removeFrom(this);
    this.game.tweens.removeFrom(this.scale);
  }
}
