import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getSliderGridLandscapeConfig() {
  const carouselH = isSquareLikeScreen() ? 0.95 : 1.125;
  return {
    // debug: { color: 0x2fc900 },
    name: 'slider',
    cells: [
      {
        name: 'carousel',
        bounds: { x: 0, y: 0.125, height: carouselH },
        align: CellAlign.CenterBottom
        // scale: CellScale.None
      },
      {
        name: 'leftArrow',
        bounds: { x: 0.035, y: 0.15, width: 0.07 }
      },
      {
        name: 'rightArrow',
        bounds: { x: 1 - 0.035 - 0.07, y: 0.15, width: 0.07 }
      },
      {
        name: 'button',
        bounds: { x: 0.2, y: 0.77, width: 0.6, height: 0.17 }
      },
      {
        name: 'hit_area',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      }
    ]
  };
}

export function getSliderGridPortraitConfig() {
  return {
    // debug: { color: 0x2fc900 },
    name: 'slider',
    cells: [
      {
        name: 'carousel',
        bounds: { x: -0.1, y: 0.402, width: 1.2 },
        align: CellAlign.CenterBottom
      },
      {
        name: 'leftArrow',
        bounds: { x: 0.035, y: 0.38, width: 0.125 }
      },
      {
        name: 'rightArrow',
        bounds: { x: 1 - 0.035 - 0.125, y: 0.38, width: 0.125 }
      },
      {
        name: 'button',
        bounds: { x: 0.2, y: 0.855, width: 0.6, height: 0.095 }
      },
      {
        name: 'hit_area',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      }
    ]
  };
}
