/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGiftsGridConfig } from '../../../configs/grid-configs';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { tweenToCell } from '../../../utils';
import { GiftView } from './gift-view';

export class GiftSetView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._gifts = [];
    this._chosenGift = null;

    this.build(this.getGridConfig());
    lego.event
      .on(ModelEvents.BoardModel.GiftsUpdate, this._onGiftsUpdate, this)
      .on(ModelEvents.BoardModel.ChosenGiftUpdate, this._onChosenGiftUpdate, this);
  }

  get name() {
    return 'GiftSetView';
  }

  get gifts() {
    return this._gifts;
  }

  getGridConfig() {
    return getGiftsGridConfig();
  }

  _onGiftsUpdate(gifts) {
    this._buildGifts(gifts);
  }

  _buildGifts(gifts) {
    gifts.forEach(g => {
      const gift = new GiftView(g);
      gift.onChoose.add(this._onGiftChoose, this);
      this.setChild(g.name, gift);
      this._gifts.push(gift);
      gift.show();
    });
  }

  _onGiftChoose(name) {
    lego.event.emit(ViewEvents.GiftView.Choose, name);
  }

  _onChosenGiftUpdate(gift) {
    this._hideOtherGifts(gift.name);
  }

  _hideOtherGifts(name) {
    this._gifts.forEach(g => {
      if (g.name !== name) {
        g.destroy();
      } else {
        tweenToCell(this, g, 'chosen_gift', 500);
      }
    });
  }
}
