import { lego } from '@armathai/lego';
import { GameState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getViewByProperty, handMoveTw, handScaleTw, searchAtlasByFrame } from '../../utils';
import { randomInt } from '../../utils/number/random-int';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0, 0);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    this._removeTw();
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    this._removeTw();
    this._positions = this._onGameStateUpdate();
    this._currentPoint = randomInt(0, this._positions.length - 1);

    this._firstTimeShow();
  }

  _firstTimeShow() {
    this._positions = this._onGameStateUpdate();
    const point = this._positions[this._currentPoint];
    this.scale.set(1);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.angle = -20;
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    handScaleTw(this).onComplete.addOnce(() => {
      this._currentPoint += 1;
      if (this._currentPoint === this._positions.length) {
        this._currentPoint = 0;
      }
      this._moveHand(this._positions[this._currentPoint]);
    });
  }

  _moveHand(pos) {
    handMoveTw(this, pos).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _getChooseBtnHintPositions() {
    const boardView = getViewByProperty('name', 'SliderView');
    const pos = this.parent.toLocal(boardView.parent.toGlobal(boardView.getHintPosition()), undefined);
    return [pos];
  }

  _getGiftsHintPositions() {
    const giftSetView = getViewByProperty('name', 'GiftSetView');
    const { gifts } = giftSetView;
    return gifts.map(b => {
      const { parent, position } = b;
      return this.parent.toLocal(new Phaser.Point(position.x, position.y), parent);
    });
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
  }

  _onGameStateUpdate() {
    const { state } = store.game;
    let arr = [];
    switch (state) {
      case GameState.Choose:
        arr = [...this._getChooseBtnHintPositions()];
        break;
      case GameState.Gift:
        arr = [...this._getGiftsHintPositions()];
        break;

      default:
        break;
    }
    return arr;
  }
}
