import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getCTALoseGridLandscapeConfig() {
  const paddingY = isSquareLikeScreen() ? 0 : 0.3;
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'play_button',
        bounds: { x: 0.05, y: 0.65, height: 0.27, width: 0.4 }
      },
      {
        name: 'popup',
        bounds: { x: 0.05, y: 0.35, width: 0.4, height: 0.3 }
      },
      {
        name: 'characters',
        bounds: { x: 0.4, y: 0.05, width: 0.65, height: 0.95 },
        align: CellAlign.CenterBottom
      },
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'bg',
        scale: CellScale.Envelop,
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        padding: { y: paddingY }
      }
    ]
  };
}

export function getCTALoseGridPortraitConfig() {
  const paddingY = isSquareLikeScreen() ? 0.2 : 0;
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'play_button',
        bounds: { x: 0, y: 0.8, width: 1, height: 0.2 }
      },
      {
        name: 'popup',
        bounds: { x: 0, y: 0.1475, width: 1, height: 0.17 },
        offset: { y: 20 }
      },
      {
        name: 'characters',
        bounds: { x: 0, y: 0.335, width: 1, height: 0.665 },
        align: CellAlign.CenterBottom
      },
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        padding: { x: -0.75, y: paddingY },
        scale: CellScale.Envelop
      }
    ]
  };
}
