import { lego } from '@armathai/lego';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { gameChooseResultStateGuard } from '../../guards/game/game-choose-result-state-guard';
import { gameChooseStateGuard } from '../../guards/game/game-choose-state-guard';
import { gameGiftReactionStateGuard } from '../../guards/game/game-gift-reaction-state-guard';
import { gameGiftStateGuard } from '../../guards/game/game-gift-state-guard';
import { gameModelGiftStateGuard } from '../../guards/game/game-model-gift-state-guard';
import { gameModelGuard } from '../../guards/game/game-model-guard';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { nextTutorialSequenceCommand } from '../ad/tutorial/next-tutorial-sequence-command';
import { showTutorialSequenceCommand } from '../ad/tutorial/show-tutorial-sequence-command';
import { initGiftsCommand } from '../board/init-gifts-command';

export function onGameModelStateUpdateCommand() {
  lego.command
    .guard(gameModelGiftStateGuard)
    .execute(initGiftsCommand)

    .guard(gameModelGuard, hintModelGuard, gameChooseStateGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand, startHintVisibilityTimerCommand)

    .guard(gameModelGuard, hintModelGuard, gameGiftStateGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand, startHintVisibilityTimerCommand)

    .guard(gameModelGuard, hintModelGuard, gameChooseResultStateGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand)

    .guard(gameModelGuard, hintModelGuard, gameGiftReactionStateGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand)

    .guard(tutorialModelGuard, gameGiftStateGuard)
    .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand);
}
