import { lego } from '@armathai/lego';
import { GameState } from '../../constants';
import { setChosenMaidenCommand } from './set-chosen-maiden-command';
import { setGameStateCommand } from './set-game-state-command';

export function onMaidenChooseCommand(uuid) {
  lego.command
    .payload(GameState.ChooseResult)
    .execute(setGameStateCommand)

    .payload(uuid)
    .execute(setChosenMaidenCommand);
}
