import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isNarrowScreen, isSquareLikeScreen } from '../../utils';

export function getCTANoLoseGridLandscapeConfig() {
  const align = isSquareLikeScreen() ? CellAlign.CenterBottom : CellAlign.CenterTop;
  const popupH = isSquareLikeScreen() ? 0.3 : 0.275;
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'play_button',
        bounds: { x: 0.06, y: 0.5, height: 0.265, width: 0.4 }
      },
      {
        name: 'popup',
        bounds: { x: 0.4, y: 0.035, width: 0.26, height: popupH },
        scale: CellScale.ShowAll
      },
      {
        name: 'characters',
        bounds: { x: 0.465, y: 0, width: 0.5, height: 1.05 },
        offset: { y: 15 },
        align
      },
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'bg',
        scale: CellScale.Envelop,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

export function getCTANoLoseGridPortraitConfig() {
  const charY = isNarrowScreen() ? 0.3 : 0.23;
  const popupY = isNarrowScreen() ? 0.2 : 0.15;
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'play_button',
        bounds: { x: 0, y: 0.82, width: 1, height: 0.18 }
      },
      {
        name: 'popup',
        bounds: { x: 0, y: popupY, width: 0.55, height: 0.225 },
        offset: { x: 15 },
        scale: CellScale.ShowAll
      },
      {
        name: 'characters',
        bounds: { x: 0, y: charY, width: 1, height: 1 - charY },
        align: CellAlign.CenterBottom,
        scale: CellScale.ShowAll
      },
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      }
    ]
  };
}
