import { lego } from '@armathai/lego';
import { CTAType, GameOverReasons } from '../../../constants';
import { setCTATypeCommand } from './set-cta-type-command';
import { showCtaCommand } from './show-cta-command';

export function ctaIdleTimeCommand() {
  lego.command
    //
    .payload(CTAType.Lose)
    .execute(setCTATypeCommand)

    .payload(GameOverReasons.Idled, 0)
    .execute(showCtaCommand);
}
