/* eslint-disable camelcase */
/* eslint-disable class-methods-use-this */
import { getCTABubbleImageConfig } from '../../configs/image-configs';
import { getCtaNoLoseHandcuffTextConfig, getCtaNoLoseTextConfig } from '../../configs/text-configs';
import { makeSpine } from '../../utils';
import { Container } from '../../utils/container';
import { Bubble } from '../bubble';

export class CTAMainViewCharacter extends Container {
  constructor(config, isHandcuffed) {
    super();

    this._config = config;
    this._isHandcuffed = isHandcuffed;
    this._build();
  }

  getBounds() {
    const { x, y, width, height } = this._getBoundsConfig()[this._config.name];
    return new Phaser.Rectangle(x, y, width, height);
  }

  rebuild() {
    this._bubble && this._setBubblePosition();
  }

  show() {
    this.game.add.tween(this._bubble).to({ alpha: 1 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
    this.game.add
      .tween(this._bubble.scale)
      .from({ x: 0, y: 0 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
  }

  _build() {
    this._buildPopup();
    this._buildSpine();

    // const { x, y, width, height } = this.getBounds();
    // const gr = this.game.add.graphics();
    // gr.beginFill(0xff0000, 0.5);
    // gr.drawRect(x, y, width, height);
    // gr.endFill();
    // this.addChild(gr);
  }

  _buildSpine() {
    const { spineData, scale } = this._config.viewConfig;
    const { handcuffedTrack0, blushTrack0, heartsTrack1, happyTrack0 } = spineData.animations;
    const spine = makeSpine(spineData.config);
    spine.setToSetupPose();

    if (this._isHandcuffed) {
      spine.setAnimationByName(0, blushTrack0, true);
      spine.setAnimationByName(1, handcuffedTrack0, true);
      spine.setAnimationByName(2, heartsTrack1, true);
    } else {
      spine.setAnimationByName(0, happyTrack0, true);
      spine.setAnimationByName(1, heartsTrack1, true);
    }

    spine.scale.copyFrom(scale);
    spine.position.x = 50 * spine.scale.x;
    this.addChild((this._spine = spine));
  }

  _buildPopup() {
    const textConfig = this._isHandcuffed ? getCtaNoLoseHandcuffTextConfig : getCtaNoLoseTextConfig;

    this._bubble = new Bubble({ imageConfig: getCTABubbleImageConfig, textConfig });
    this._setBubblePosition();
    this._bubble.alpha = 0;
    this.addChild(this._bubble);
  }

  _setBubblePosition() {
    const { x, y } = this._getBubblePositionConfig()[this._config.name];
    this._bubble.position.set(x, y);
  }

  _getBubblePositionConfig() {
    return {
      Wine_girl: {
        x: -110,
        y: -695
      },
      Jewelry_girl: {
        x: -220,
        y: -695
      },
      Flower_girl: {
        x: -220,
        y: -695
      }
    };
  }

  _getBoundsConfig() {
    return {
      Wine_girl: {
        x: -290,
        y: -770,
        width: 550,
        height: 765
      },
      Jewelry_girl: {
        x: -380,
        y: -770,
        width: 560,
        height: 765
      },
      Flower_girl: {
        x: -380,
        y: -770,
        width: 560,
        height: 765
      }
    };
  }
}
