import { getCtaPopupTitleTextConfig } from '../configs/text-configs';
import { fitText, makeImage, makeText } from '../utils';
import { Container } from '../utils/container';

export class Bubble extends Container {
  constructor(config) {
    super();

    const { imageConfig, textConfig, isLost = false } = config;

    this._buildBg(imageConfig);
    this._buildText(textConfig, isLost);
    !isLost && this._buildTitle();
  }

  _buildBg(imageConfig) {
    const bg = makeImage(imageConfig.call());
    this.addChild((this._bg = bg));
  }

  _buildTitle() {
    const title = makeText(getCtaPopupTitleTextConfig());
    title.anchor.set(0, 0.5);

    title.position.set(-this._bg.width / 2.5, -this._bg.height / 3 - title.height / 4 + 10);
    this.addChild(title);
  }

  _buildText(textConfig, isLost) {
    const text = makeText(textConfig.call(this, this._bg.width));
    text.anchor.set(0, 0.5);
    text.position.set(-this._bg.width / 2.5, 17);

    if (isLost) {
      text.anchor.set(0.5);
      text.position.set(0, 0);
    }

    text.lineSpacing = -8;
    this.addChild(text);

    fitText(text, this._bg.width * 0.9, this._bg.height * 0.7);
  }
}
