import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getBoardGridLandscapeConfig() {
  return {
    // debug: { color: 0x2a5900 },
    name: 'board',
    cells: [
      {
        name: 'gifts',
        bounds: { x: 0.05, y: 0.35, width: 0.43, height: 0.58 }
      },
      {
        name: 'cell',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.None
      },
      {
        name: 'maiden',
        bounds: { x: 0.5, y: 0.125, height: 1, width: 0.4 }
      }
    ]
  };
}

export function getBoardGridPortraitConfig() {
  const giftsX = isSquareLikeScreen() ? 0.225 : 0.125;
  const giftsY = isSquareLikeScreen() ? 0.7 : 0.68;
  const giftsW = isSquareLikeScreen() ? 0.55 : 0.75;
  const giftsH = isSquareLikeScreen() ? 0.25 : 0.275;

  return {
    // debug: { color: 0x2a5900 },
    name: 'board',
    cells: [
      {
        name: 'gifts',
        bounds: { x: giftsX, y: giftsY, width: giftsW, height: giftsH }
      },
      {
        name: 'cell',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.None
      },
      {
        name: 'maiden',
        bounds: { x: -0.1, y: 0.402, width: 1.2 },
        align: CellAlign.CenterBottom
      }
    ]
  };
}
