import { isSquareLikeScreen } from '../../utils';

export function getUIGridLandscapeConfig() {
  const height = isSquareLikeScreen() ? 0.1 : 0.135;
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        bounds: { x: 0.05, y: 0.135, height, width: 0.16 },
        offset: { x: 20, y: 20 }
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        bounds: { x: 0, y: 0.135, width: 1, height: 0.075 },
        offset: { y: 20 }
      }
    ]
  };
}
