// CTA
export function getCtaPlayButtonTextConfig(text, fill = '#ffffff') {
  return {
    text,
    style: {
      font: FONT2,
      fontSize: 40,
      fill,
      stroke: '#000000',
      strokeThickness: 5
    }
  };
}

export function getCtaRetryButtonTextConfig() {
  return {
    text: 'Retry',
    y: -5,
    style: {
      font: FONT2,
      fontSize: 75,
      fill: '#ffffff',
      stroke: '#159b1c',
      strokeThickness: 1,
      shadow: {
        x: 1,
        y: 2,
        color: '#159b1c',
        blur: 4,
        shadowStroke: true,
        shadowFill: true
      }
    }
  };
}

