import { giftsConfig } from '../../../configs';
import { ObservableModel } from '../../observable-model';
import { GiftModel } from './gift-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');
    this._gifts = [];
    this._chosenGift = null;

    this.makeObservable();
  }

  get gifts() {
    return this._gifts;
  }

  set gifts(value) {
    this._gifts = value;
  }

  get chosenGift() {
    return this._chosenGift;
  }

  set chosenGift(value) {
    this._chosenGift = value;
  }

  initGifts() {
    const n = CI_API.Globals.PARAMS.handcuffs_cta ? giftsConfig.length : giftsConfig.length - 1;
    const arr = [];
    for (let i = 0; i < n; i += 1) {
      const gift = new GiftModel(giftsConfig[i]);
      arr.push(gift);
    }

    this._gifts = [...arr];
  }

  getGiftByName(name) {
    return this._gifts.find(g => g.name === name);
  }
}
