/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { BoardView } from './board/board-view';
import { SliderView } from './slider/slider-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._slider = null;
    this._boardView = null;

    this._build();
    lego.event.on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this);
    lego.event.on(ModelEvents.GameModel.ChosenMaidenUpdate, this._onChosenMaidenUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  _onChosenMaidenUpdate() {
    this._slider.hide();
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildSlider();
  }

  // BOARD
  _onBoardUpdate(board) {
    board ? this._buildBoard() : this._destroyBoard();
  }

  _buildBoard() {
    this.setChild('board', (this._boardView = new BoardView()));
  }

  _destroyBoard() {
    // this._boardView.destroy();
  }

  _buildSlider() {
    const slider = new SliderView();
    this.setChild('slider', (this._slider = slider));
  }
}
