/* eslint-disable camelcase */
/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCTABubbleImageConfig } from '../../configs/image-configs';
import { getRevealedGirlSpineConfig, Spines } from '../../configs/spine-configs';
import { getCtaNoLoseHandcuffTextConfig, getCtaNoLoseTextConfig } from '../../configs/text-configs';
import { ViewEvents } from '../../events/view-events';
import { makeSpine } from '../../utils';
import { Container } from '../../utils/container';
import { Bubble } from '../bubble';

export class CTARevealedCharacter extends Container {
  constructor(maiden, isHandcuffed) {
    super();
    this._config = maiden;
    this._isHandcuffed = isHandcuffed;
    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(-340, -800, 550, 790);
  }

  show() {
    this.game.add.tween(this._bubble).to({ alpha: 1 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);
    this.game.add
      .tween(this._bubble.scale)
      .from({ x: 0, y: 0 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false)
      .onStart.add(() => {
        lego.event.emit(ViewEvents.BubbleComponent.ShowComplete);
      });
  }

  _build() {
    this._buildPopup();
    this._buildSpine();

    // const gr = this.game.add.graphics();
    // gr.beginFill(0xff0000, 0.5);
    // gr.drawRect(this.getBounds().x, this.getBounds().y, this.getBounds().width, this.getBounds().height);
    // gr.endFill();
    // this.addChild(gr);
  }

  _buildPopup() {
    const textConfig = this._isHandcuffed ? getCtaNoLoseHandcuffTextConfig : getCtaNoLoseTextConfig;

    this._bubble = new Bubble({ imageConfig: getCTABubbleImageConfig, textConfig });
    this._setBubblePosition();
    this._bubble.alpha = 0;
    this.addChild(this._bubble);
  }

  _buildSpine() {
    const anim = this._getMaidenAnim(this._config.name);
    const spine = makeSpine(getRevealedGirlSpineConfig());

    spine.setToSetupPose();
    spine.setAnimationByName(0, anim, true);
    this._isHandcuffed && spine.setAnimationByName(1, Spines.RevealedGirl.Animations.Handcuffed, true);
    spine.setAnimationByName(2, Spines.RevealedGirl.Animations.Kiss, true);

    this.addChild(spine);
  }

  _setBubblePosition() {
    this._bubble.position.set(-180, -695);
  }

  _getMaidenAnim(name) {
    let anim = Spines.RevealedGirl.Animations.Flower;
    switch (name) {
      case 'Wine_girl':
        anim = Spines.RevealedGirl.Animations.Wine;
        break;
      case 'Jewelry_girl':
        anim = Spines.RevealedGirl.Animations.Jewels;
        break;

      default:
        break;
    }
    return anim;
  }
}
